.center{
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: center;
       justify-content: center;
   -ms-flex-align: center;
       align-items: center;
}

.newsImage{
   height: 300px;
   width: 500px;
}
.post{
   -webkit-box-shadow:  0 1px 5px rgba(0, 0, 0, 0.15);
           box-shadow:  0 1px 5px rgba(0, 0, 0, 0.15);
   padding: 0px 30px 0px 30px;
   margin-top: 30px;
   background: lightgray;
}
.none{
   display: none;
}

.loading{
   height:80px;
   width: 80px;
   border-radius: 50%;
   border: 1px solid lightgray;
   border-left-color: rgb(63, 63, 245);
   -webkit-animation: load infinite 1s;
           animation: load infinite 1s;
   position: absolute;
   top: 40%;
   left: 45%;
}

.source{
   color: blue;
}
.time{
   color:gray;
}

@-webkit-keyframes load{
   to{
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg)
   }
}

@keyframes load{
   to{
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg)
   }
}
input{
   height: 20px;
   width: 300px;
   padding: 3px;
}

.container{
   display: grid;
   grid-template-columns: 1fr 1fr;
   grid-gap:20px;
   margin: 10px 30px;
}

.searchResult{
   background: #333;
}

.submitBtn{
   display: none;
   cursor: pointer;
   background: rgb(96, 96, 253);
   padding: 6px 15px;
   border: none;
   border-radius: 5px;
   font-size: 16px;
}

.Heading{
   background-color:rgb(68, 68, 70);
background-image: -webkit-linear-gradient(60deg, #445 12%, transparent 12.5%, transparent 87%, rgb(59, 59, 78) 87.5%, #445),
-webkit-linear-gradient(300deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
-webkit-linear-gradient(60deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
-webkit-linear-gradient(300deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
-webkit-linear-gradient(30deg, rgb(64, 64, 65) 25%, transparent 25.5%, transparent 75%, rgb(82, 82, 90) 75%, #99a), 
-webkit-linear-gradient(30deg, rgb(75, 75, 82) 25%, transparent 25.5%, transparent 75%, rgb(78, 78, 85) 75%, rgb(97, 97, 102));
background-image: -o-linear-gradient(60deg, #445 12%, transparent 12.5%, transparent 87%, rgb(59, 59, 78) 87.5%, #445),
-o-linear-gradient(300deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
-o-linear-gradient(60deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
-o-linear-gradient(300deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
-o-linear-gradient(30deg, rgb(64, 64, 65) 25%, transparent 25.5%, transparent 75%, rgb(82, 82, 90) 75%, #99a), 
-o-linear-gradient(30deg, rgb(75, 75, 82) 25%, transparent 25.5%, transparent 75%, rgb(78, 78, 85) 75%, rgb(97, 97, 102));
background-image: linear-gradient(30deg, #445 12%, transparent 12.5%, transparent 87%, rgb(59, 59, 78) 87.5%, #445),
linear-gradient(150deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
linear-gradient(30deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
linear-gradient(150deg, #445 12%, transparent 12.5%, transparent 87%, #445 87.5%, #445),
linear-gradient(60deg, rgb(64, 64, 65) 25%, transparent 25.5%, transparent 75%, rgb(82, 82, 90) 75%, #99a), 
linear-gradient(60deg, rgb(75, 75, 82) 25%, transparent 25.5%, transparent 75%, rgb(78, 78, 85) 75%, rgb(97, 97, 102));
background-size:80px 140px;
background-position: 0 0, 0 0, 40px 70px, 40px 70px, 0 0, 40px 70px;
padding: 40px;
color: white;
}

button{
   padding: 6px 15px;
   border: none;
   border-radius: 5px;
   font-size: 16px;
   cursor: pointer;
   margin: 0px 10px;
   color: white;
}

.showBtn{
   display: none;
   background: rgb(96, 96, 253);
}
.hideBtn{
   display: none;
   background: rgb(253, 96, 96);
}

.search{
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: center;
       justify-content: center;
   padding: 30px;
}
.error{
   background: rgb(241, 76, 76);
   padding: 15px;
   font-size: 24px;
   font-weight: bold;
   text-align: center;
   margin-left: 300px;
   margin-right: 300px;
   border-radius: 5px;
}
.top20{
   text-align: center;
   color: rgb(86, 86, 236);
   margin-top: 50px;
}
.post{
   color: #333;
}



